import * as types from '../constants/types'

export const userMutations = {
    [types.SET_USER] (state, action) {
        state.id = action.id;
        state.phone = action.phone;
        state.token = action.token;
    },
    // 清除数据
    // ===============
    [types.CLEAR_USER] (state) {
        state.id = 0;
        state.phone = '';
        state.token = '';
    }
}



